.data-section {
  margin-left: 440px;
  height: 100vh;
  background-color: #f8f9fa;
  overflow-y: scroll;
  padding: 60px;
  font-weight: 400;
}

.subject__title {
  font-size: 36px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.subject__title__code {
  background-color: #94D82D;
  border-radius: 30px;
  font-size: 24px;
  color: white;
  margin-right: 16px;
  letter-spacing: 1px;
  font-weight: 500;
  padding: 6px 14px;
}

.subject__title__text {
  font-size: 36px;
  color: #343A40;
}

.chair {
  margin-bottom: 40px;
}

.chair__title {
  font-size: 24px;
  color: #495057;
  font-weight: 500;
}

.chair__title__pre {
  color: #ADB5BD;
  font-weight: 400;
}

.item {
  background-color: #f1f3f5;
  border-radius: 3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 12px;
  height: 50px;
}

.item__code {
  background-color: #dee2e6;
  color: #868e96;
  border-radius: 3px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-weight: 500;
}

.item__place {
  -ms-flex-preferred-size: 10%;
      flex-basis: 10%;
}

.item__schedule {
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
}

.item__professor {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  white-space: nowrap;
}

.item__mode {
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: white;
}

.item__mode.regular { background-color: #a9e34b; }
.item__mode.virtual { background-color: #329af0; }
.item__mode.distancia { background-color: #fcc419; }


